@use 'base/mixins' as m;
@use 'base/variables' as v;

.vacante-laboral {
    padding: 3rem;
    margin-bottom: 2rem;

    &:last-of-type {
        margin: 0;
    }
    @include m.tablet {
        margin: 0;
    }
    -webkit-box-shadow: 0px 0px 10px -5px lighten(v.$secundario, 10%);
    -moz-box-shadow: 0px 0px 10px -5px lighten(v.$secundario, 10%);
    box-shadow: 0px 0px 10px -5px lighten(v.$secundario, 10%);

    &__heading {
        @include m.headingBorder(v.$negro, v.$primario);
        font-size: 2.5rem;
    }
    &__texto {
        font-size: 1.5rem;
    }
    &__expiracion {
        color: v.$primario;
        font-weight: 700;
        font-size: 1.8rem;
    }
    &__enlace {
        @include m.boton(v.$primario, v.$blanco);
        margin: 0;
    }
}
.vacante-info {
    @include m.tablet {
        @include m.grid(3, 2rem);
        border: 1px solid v.$primario;
    }
    &__texto {
        span {
            font-weight: 700;
        }
    }
}