html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 3rem;
  font-family: "Mulish", sans-serif;
}
@media (min-width: 768px) {
  h1 {
    font-size: 4.4rem;
  }
}

h2 {
  font-size: 2.5rem;
  font-family: "Mulish", sans-serif;
}
@media (min-width: 768px) {
  h2 {
    font-size: 4rem;
  }
}

h3 {
  font-size: 2rem;
  font-family: "Mulish", sans-serif;
}
@media (min-width: 768px) {
  h3 {
    font-size: 3rem;
  }
}

a {
  font-family: "Roboto", sans-serif;
  text-decoration: none;
  color: #000000;
}

p {
  font-family: "Mulish", sans-serif;
  color: #000000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.m-5 {
  margin-bottom: 5rem;
}

.mt-5 {
  margin-top: 5rem;
}

.m-3 {
  margin-bottom: 3rem;
}

.bgColor {
  background-color: snow;
}

.no-margin {
  margin: 0;
}

.no-padding {
  padding: 0;
}

.dblock {
  display: block;
}

.centrado {
  text-align: center;
}

.dflex {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.centrar-contenedor {
  max-width: 120rem;
  margin: 0 auto;
}

.btn-actualizar {
  background-color: #e0ca09;
  padding: 1.5rem 4rem;
  color: #000000;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color scale;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .btn-actualizar {
    width: auto;
    font-size: 2rem;
  }
}
.btn-actualizar:hover {
  transform: scale(1.1);
  transition-duration: 0.3s;
  background-color: #af9e07;
}

.btn-eliminar {
  background-color: #e41616;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color scale;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .btn-eliminar {
    width: auto;
    font-size: 2rem;
  }
}
.btn-eliminar:hover {
  transform: scale(1.1);
  transition-duration: 0.3s;
  background-color: #b51212;
}

.btn-actualizar,
.btn-eliminar {
  width: 20rem;
  font-weight: 400;
  margin: 0.5rem auto;
  cursor: pointer;
}

.boton-filtro {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
}

.webp .heading {
  background-image: url(../img/Frontis_muni.webp);
  background-repeat: no-repeat;
}

.no-webp .heading {
  background-image: url(../img/Frontis_muni.jpg);
  background-repeat: no-repeat;
}

.heading {
  background-image: url(../img/Frontis_muni.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
  height: 30rem;
  position: relative;
  margin: 0;
}
@media (min-width: 768px) {
  .heading {
    background-position: 50% 88%;
  }
}
@media (min-width: 1024px) {
  .heading {
    height: 35rem;
    background-position: 50% 85%;
  }
}
.heading__contenido {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  padding-top: 1rem;
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  align-items: center;
  grid-auto-flow: column;
}
@media (min-width: 768px) {
  .heading__contenido {
    padding-top: 2rem;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: unset;
  }
}
.heading__imagen {
  display: none;
}
@media (min-width: 768px) {
  .heading__imagen {
    grid-column: 1/2;
    display: block;
    width: 20rem;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .heading__imagen {
    width: 40rem;
  }
}
.heading__titulo {
  text-align: center;
}
.heading__heading {
  display: inline-block;
  color: #fff;
  text-transform: uppercase;
  font-size: 3rem;
}
.heading__heading::after {
  content: "";
  background-color: #e41616;
  height: 0.3rem;
  display: block;
}
@media (min-width: 768px) {
  .heading__heading {
    font-size: 4rem;
  }
}
@media (min-width: 1024px) {
  .heading__heading {
    font-size: 5rem;
  }
}
.heading__texto {
  display: none;
  color: #fff;
  text-transform: uppercase;
}
@media (min-width: 1024px) {
  .heading__texto {
    display: block;
    font-size: 1.5rem;
  }
}
.heading__sociales {
  text-align: center;
}
.heading__social {
  color: #fff;
}

.heading-crud {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  border-bottom: 1px solid #e41616;
}
.heading-crud__titulo {
  text-align: center;
  text-transform: uppercase;
}
.heading-crud__heading {
  color: #000000;
}
.heading-crud__imagen {
  width: 30rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.dark-mode-boton {
  width: 3rem;
  filter: invert(100%);
}
.dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .dark-mode-boton {
    width: 4rem;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.5s linear;
  background-color: #ea2929;
}
@media (min-width: 768px) {
  .navegacion {
    visibility: visible;
    height: auto;
    opacity: 1;
  }
}
.navegacion__flex {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 2rem;
}
@media (min-width: 768px) {
  .navegacion__flex {
    flex-direction: row;
    justify-content: space-between;
  }
}
.navegacion__enlace {
  padding: 2rem;
  color: #fff;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
  transition-property: color background-color;
  transition-duration: 0.3s;
}
@media (min-width: 1024px) {
  .navegacion__enlace {
    font-size: 2rem;
  }
}
.navegacion__enlace:hover {
  background-color: #fff;
  color: #000000;
}

.mobile-menu {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .mostrar {
    display: block;
  }
}

.sobre-nosotros {
  background-color: snow;
  padding: 5rem 0;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .sobre-nosotros {
    margin-top: 0;
  }
}
.sobre-nosotros__heading {
  text-align: center;
  margin: 0;
}
.sobre-nosotros__texto {
  display: inline-block;
  color: #000000;
  text-align: center;
  margin: 0;
}
.sobre-nosotros__texto::after {
  content: "";
  background-color: #e41616;
  height: 0.3rem;
  display: block;
}
.sobre-nosotros__descripcion {
  color: #000000;
  text-align: justify;
  padding: 0 2rem;
}
.sobre-nosotros__enlace {
  background-color: #e41616;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color scale;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .sobre-nosotros__enlace {
    width: auto;
    font-size: 2rem;
  }
}
.sobre-nosotros__enlace:hover {
  transform: scale(1.1);
  transition-duration: 0.3s;
  background-color: #b51212;
}
.sobre-nosotros__flex {
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .sobre-nosotros__flex {
    justify-content: flex-end;
  }
}

.servicios {
  text-align: center;
}
@media (min-width: 768px) {
  .servicios {
    margin: 5rem 0;
  }
}
.servicios h2 {
  display: inline-block;
  color: #000000;
}
.servicios h2::after {
  content: "";
  background-color: #e41616;
  height: 0.3rem;
  display: block;
}
@media (min-width: 768px) {
  .servicios__grid {
    display: grid;
