}
.servicio__enlace {
  background-color: #e41616;
  padding: 1.5rem 4rem;
  color: #fff;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color scale;
  transition-duration: 0.3s;
  text-align: center;
  border-radius: 1rem;
  margin: 0;
  display: block;
}
@media (min-width: 768px) {
  .servicio__enlace {
    width: auto;
