@use 'base/variables' as v;
@use 'base/mixins' as m;

.derecha {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
}
.dark-mode-boton {
    width: 3rem;
    filter: invert(100%);

    &:hover {
        cursor: pointer;
    }
    @include m.tablet { 
        width: 4rem;
     }
}
.navegacion {
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .3s linear, height .5s linear;
    background-color: lighten(v.$primario, 5%);
    @include m.tablet { 
        visibility: visible;
        height: auto;
        opacity: 1;
     }
    &__flex {
        display: flex;
        flex-direction: column;
        text-align: center;
        gap: 2rem;
        @include m.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }
    &__enlace {
        padding: 2rem;
        color: v.$blanco;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: 700;
        transition-property: color background-color;
        transition-duration: .3s;
        @include m.desktop {
            font-size: 2rem;
        }
        &:hover {
            background-color: v.$blanco;
            color: v.$negro;
        }
    }
}
.mobile-menu {
    display: flex;
    justify-content: center;
    @include m.tablet { 
        display: none;
     }
}
.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
    margin: 0 auto;
    @include m.tablet { 
        display: block;
     }
}