@use 'base/variables' as v;
@use 'base/mixins' as m;

.copyright {
    background-color: darken(v.$primario, 10%);
    margin: 0;
    padding: 1rem 0;
    color: v.$blanco;
    text-transform: uppercase;
    font-size: 1rem;
    font-weight: 700;
    text-align: center;
    @include m.tablet {
        font-size: 1.5rem;
    }
}