@use 'base/variables' as v;
@use 'base/mixins' as m;

.sobre-nosotros {
    background-color: lighten(v.$primario, 50%);
    padding: 5rem 0;
    margin-top: 5rem;
    @include m.tablet {
        margin-top: 0;
    }
    &__heading {
        text-align: center;
        margin: 0;
    }
    &__texto {
        @include m.headingBorder(v.$negro, v.$primario);
        text-align: center;
        margin: 0;
    }
    &__descripcion {
        color: v.$negro;
        text-align: justify;
        padding: 0 2rem;
    }
    &__enlace {
        @include m.boton(v.$primario, v.$blanco);
    }
    &__flex {
        display: flex;
        justify-content: center;
        @include m.tablet {
            justify-content: flex-end;
        }
    }
}