@use 'base/mixins' as m;
@use 'base/variables' as v;

.servicios-omil {
    &__info {
        text-align: center;
    }
    &__heading {
        @include m.headingBorder(v.$negro, v.$primario);
    }
}
.bgColor {
    padding: 5rem 0;
}
.servicio-omil {
    &__flex {
        text-align: center;
            @include m.desktop {
            display: grid;
            grid-template-columns: 1fr 3fr;
            column-gap: 2rem;
            align-items: center;
            text-align: left;
            -webkit-box-shadow: 4px 4px 25px 0px rgba(230,230,230,1);
            -moz-box-shadow: 4px 4px 25px 0px rgba(230,230,230,1);
            box-shadow: 4px 4px 25px 0px rgba(230,230,230,1);
            padding: 5rem;
        }
    }
    &__heading {
        @include m.headingBorder(v.$negro, v.$secundario);
        @include m.desktop {
            font-size: 2.5rem;
        }
    }
    &__texto {
        text-align: justify;
    }
}