@use 'base/variables' as v;
@use 'base/mixins' as m;

.equipo {
    text-align: center;
    &__heading {
        @include m.headingBorder(v.$negro, v.$primario);
    }
    &__grid {
        @include m.grid(2, 2rem);
        @include m.tablet {
            @include m.grid(4, 4rem);
        }
    }
}
.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    &__heading {
        @include m.headingBorder(v.$negro, v.$secundario);
        font-size: 2rem;
    }
    &__texto {
        font-weight: 700;
        margin: 0;
    }
}