@use 'base/mixins' as m;
@use 'base/variables' as v;

.alerta {
    padding: 1rem;
    margin: 1rem;
    text-transform: uppercase;
    font-family: v.$fuentePrincipal;

    &.error {
        background-color: v.$primario;
        color: v.$blanco;
        font-weight: 700;
    }
    &.exito {
        background-color: v.$secundario;
        font-weight: 700;
    }
}

table.vacantes-crud {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    font-family: v.$fuentePrincipal;

    thead {
        background-color: v.$primario;
        th {
            color: v.$blanco;
            padding: 2rem;
            margin: 0 auto;
        }
    }
    
    tbody {
        td {
            padding: 1rem;
            margin: 0 auto;
        }
    }
}
.iniciar-sesion {
    @include m.headingBorder(v.$negro, v.$primario);
}
.grid-admin {
    @include m.grid(2, 5rem);
    align-items: center;
}