@use 'base/variables' as v;
@use 'base/mixins' as m;

.alianzas {
    text-align: center;
    &__texto {
        @include m.headingBorder(v.$negro, v.$primario);
        margin-bottom:2rem;
    }
    &__grid {
        @include m.grid(1, 2rem);
        align-items: center;
        padding: 2rem;
        @include m.tablet {
            @include m.grid(2, 2rem);
        }
        @include m.desktop {
            @include m.grid(4, 2rem);
        }
    }
}