@use 'base/variables' as v;
@use 'base/mixins' as m;

.servicios {
    text-align: center;
    @include m.tablet {
        margin: 5rem 0;
    }
    h2 {
        @include m.headingBorder(v.$negro, v.$primario);
    }
    &__grid {
        @include m.tablet {
            @include m.grid(2, 0);
        }
    }
}