@use 'base/mixins' as m;
@use 'base/variables' as v;

.nosotros {
    &__info {
        text-align: center;
        margin: 5rem auto;
    }
    &__heading {
        margin: 0;
        @include m.headingBorder(v.$negro, v.$primario);
    }

}
.mision,
.vision {
    &__grid {
        @include m.tablet {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            align-items: center;
            column-gap: 2rem;
        }
    }
    &__imagen {
        width: 50rem;
    }
    &__texto {
        text-align: justify;
    }
}
.mision {
    &__texto {
        @include m.tablet {
            grid-column: 2 / 4;
        }
    }
}
.vision {
    &__grid {
        display: flex;
        flex-direction: column-reverse;
        @include m.tablet {
            display: grid;
        }
    }
    &__texto {
        @include m.tablet {
            grid-column: 1 / 3;
        }
    }
}