@use 'base/variables' as v;
@use 'base/mixins' as m;

.webp .heading{
    background-image: url(../img/Frontis_muni.webp);
    background-repeat: no-repeat;
}
.no-webp .heading{
    background-image: url(../img/Frontis_muni.jpg);
    background-repeat: no-repeat;
}
.heading {
    background-image: url(../img/Frontis_muni.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center bottom;
    height: 30rem;
    position: relative;
    margin: 0;
    @include m.tablet {
        background-position: 50% 88%;
    }
    @include m.desktop {
        height: 35rem;
        background-position: 50% 85%;
    }
    &__contenido {
        position: absolute;
        background-color: rgba(0,0,0, .5);
        padding-top: 1rem;
        height: 100%;
        width: 100%;
        display: grid;
        grid-template-rows: repeat(2, 1fr);
        align-items: center;
        grid-auto-flow: column;
        @include m.tablet {
            padding-top: 2rem;
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: unset;
        }
    }
    &__imagen {
        display: none;
        @include m.tablet {
            grid-column: 1 / 2;
            display: block;
            width: 20rem;
            margin: 0 auto;
        }
        @include m.desktop {
            width: 40rem;
        }
    }
    &__titulo {
        text-align: center;
    }
    &__heading {
        @include m.headingBorder(v.$blanco, v.$primario);
        text-transform: uppercase;
        font-size: 3rem;
        @include m.tablet {
            font-size: 4rem;
        }
        @include m.desktop {
            font-size: 5rem;
        }
    }
    &__texto {
        display: none;
        color: v.$blanco;
        text-transform: uppercase;
        @include m.desktop {
            display: block;
            font-size: 1.5rem;
        }
    }
    &__sociales {
        text-align: center;
    }
    &__social {
        color: v.$blanco;
    }
}
.heading-crud {
    @include m.grid(3, 2rem);
    align-items: center;
    border-bottom: 1px solid v.$primario;
    &__titulo {
        text-align: center;
        text-transform: uppercase;
    }
    &__heading {
        color: v.$negro;
    }
    &__imagen {
        width: 30rem;
    }
}