@use 'variables' as v;
@use 'base/mixins' as m;

h1, h2, h3 {
    margin: 0;
    line-height: 1.2;
}
h1 {
    font-size: 3rem;
    font-family: v.$fuenteSecundaria;
    @include m.tablet {
        font-size: 4.4rem;
    }
}
h2 {
    font-size: 2.5rem;
    font-family: v.$fuenteSecundaria;
    @include m.tablet {
        font-size: 4rem;
    }
}
h3 {
    font-size: 2rem;
    font-family: v.$fuenteSecundaria;
    @include m.tablet {
        font-size: 3rem;
    }
}
a {
    font-family: v.$fuentePrincipal;
    text-decoration: none;
    color: v.$negro;
}
p {
    font-family: v.$fuenteSecundaria;
    color: v.$negro;
    font-size: 1.6rem;
    line-height: 1.8;
}