@use 'base/mixins' as m;
@use 'base/variables' as v;

.contacto {
    text-align: center;
    &__heading {
        margin: 5rem auto 3rem auto;
        @include m.headingBorder(v.$negro, v.$primario);
    }
}
.formulario {
    text-align: left;
    font-family: v.$fuentePrincipal;
    margin-bottom: 5rem;

    fieldset {
        border: 1px solid v.$primario;
        border-bottom-right-radius: 1rem;
    }
    legend {
        text-transform: uppercase;
        font-size: 2.5rem;
    }
    label,
    input {
        display: block;
    }
    label {
        font-size: 2rem;
        text-transform: uppercase;
        margin: 1rem 0;
    }
    input,
    textarea,
    select {
        width: 100%;
        border: 1px solid v.$primario;
        border-bottom-right-radius: 1rem;
        padding: 1rem;
    }
    select {
        width: 30rem;
    }
    textarea {
        height: 20rem;
    }
    input[type="submit"] {
        @include m.boton(v.$primario, v.$blanco);
        &:hover {
            cursor: pointer;
        }
    }
    &__grid {
        display: grid;
        grid-template-columns: 2fr 1fr 1fr;
        gap: 3rem;
        &--2 {
            grid-template-columns: repeat(3, 1fr);
        }
    }
    &--vacantes {
        label, input {
            display: inline-block;
            width: 10rem;
        }
        &-grid {
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 3rem;
            @include m.tablet { 
                @include m.grid(3, 0);
                align-items: center;
                justify-content: center;
             }
        }
    }
}