@use 'base/mixins' as m;
@use 'base/variables' as v;

.alianza {
    align-items: center;
    transition-property: transform;
        transition-duration: .3s;
        &:hover {
            transform: scale(1.1);
        }
    &__imagen {
        width: 15rem;
        height: 10rem;
        margin: 0 auto;
    }
    &__texto {
        @include m.headingBorder(v.$negro, v.$primario);
        text-align: center;
        padding: 1.5rem 4rem;
        margin-top: 2rem;
        font-size: 2rem;
        font-weight: 400;
        display: inline-block;
        font-weight: 700;
        border: none;
        margin: 0;
        display: block;
    }
}