@use 'base/mixins' as m;
@use 'base/variables' as v;

.servicio {
    width: 95%;
    margin: 5rem auto 0 auto;
    &__imagen {
        width: 30rem;
        margin: 0 auto;
        padding: 2rem;
    }
    &__enlace {
        @include m.boton(v.$primario, v.$blanco);
        margin: 0;
        display: block;
        &--crear {
            width: 30rem;
        }
        &--admin {
            display: block;
            width: 50rem;
            margin: 2rem auto 0 auto;
        }
    }
}