@use 'base/mixins' as m;
@use 'base/variables' as v;

.m-5 {
    margin-bottom: 5rem;
}
.mt-5 {
    margin-top: 5rem;
}
.m-3 {
    margin-bottom: 3rem;
}
.bgColor {
    background-color: lighten(v.$primario, 50%);
}
.no-margin {
    margin: 0;
}
.no-padding {
    padding: 0;
}
.dblock {
    display: block;
}
.centrado {
    text-align: center;
}
.dflex {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.centrar-contenedor {
    max-width: 120rem;
    margin: 0 auto;
}