@use 'base/mixins' as m;
@use 'base/variables' as v;

.btn-actualizar {
    @include m.boton(v.$amarillo, v.$negro);
}
.btn-eliminar {
    @include m.boton(v.$primario, v.$blanco);
}

.btn-actualizar,
.btn-eliminar {
    width: 20rem;
    font-weight: 400;
    margin: .5rem auto;
    cursor: pointer;
}
.boton-filtro {
    font-family: v.$fuentePrincipal;
    font-weight: 700;
}