@use 'base/mixins' as m;
@use 'base/variables' as v;

.dark-mode {
    background-color: lighten(v.$negro, 20%);

    .servicio {
        &__imagen{
            filter: invert(100%);
        }
    }

    h1, h2, h3, a, p {
        color: v.$blanco;
    }
    .sobre-nosotros {
        background-color: lighten(v.$negro, 20%);
    }
    .bgColor {
        background-color: transparent;
    }
    .formulario {
        legend, label{
            color: v.$blanco;
        }
        input, textarea {
            background-color: lighten(v.$negro, 50%)
        }
        input::placeholder, textarea::placeholder {
                color: v.$blanco;
        }
        input[type='submit'] {
            background-color: v.$primario;
        }
    }
}