@use 'base/mixins' as m;
@use 'base/variables' as v;

.vacantes {
    text-align: center;
    &__grid {
        @include m.tablet {
            @include m.grid(2, 2rem);
        }
        @include m.desktop {
            @include m.grid(3, 2rem);
        }
    }
    &__heading {
        @include m.headingBorder(v.$negro, v.$primario);
        text-align: center;
    }
}